<template>
  <div class="follow-operation">
    <!--  搜索  -->
    <el-form
      ref="channelSearch"
      label-position="left"
      :model="channelSearch"
      inline
    >
      <el-form-item label="关键词" class="search-item">
        <el-input
          v-model="channelSearch.keyword"
          clearable
          placeholder="请输入关键词"
        />
      </el-form-item>
      <el-form-item style="float: right">
        <el-button type="primary" class="select-btn" @click="onSubmit"
          >查询</el-button
        >
        <el-button type="primary" class="select-btn" @click="resetForm"
          >重置</el-button
        >
      </el-form-item>
    </el-form>
    <!--  列表  -->
    <div class="list">
      <div class="title">
        <el-button
          type="primary"
          @click="
            $router.push({
              name: 'addCode'
            })
          "
          >新增小程序码</el-button
        >
      </div>
      <el-table
        :data="list"
        style="width: 100%"
        border
        :empty-text="hasListInfo"
      >
        <el-table-column
          fixed
          prop="num"
          label="序号"
          width="100"
          align="center"
        />
        <el-table-column prop="scene" label="场景值" align="center" />
        <el-table-column prop="remark" label="备注" align="center" />
        <el-table-column prop="page" label="路径" align="center" />
        <el-table-column prop="created_at" label="创建时间" align="center" />
        <el-table-column fixed="right" label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-button
              v-if="activeName === 'code'"
              type="text"
              size="small"
              @click="downloadCode(scope.row.id, scope.row.scene)"
              >下载
            </el-button>
            <el-button
              type="text"
              size="small"
              @click="
                rowInfo = scope.row
                delShow = true
              "
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          :current-page="currentPage"
          :page-sizes="[10, 20, 50]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
    <el-dialog
      title="删除提示"
      :visible.sync="delShow"
      width="400px"
      center
      top="30vh"
    >
      <p class="center">
        删除后，该小程序码将不存在，
        <br />请确定是否删除？
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delConfirm">确 定</el-button>
        <el-button type="primary" @click="delShow = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  getCodeList,
  deleteProgramCode,
  downloadCode
} from '@/api/assistantManage'
import styleOne from '@/assets/images/style-one.png'
import styleTwo from '@/assets/images/style-two.png'
import styleThree from '@/assets/images/style-three.png'
export default {
  data() {
    return {
      isOpen: false,
      list: [],
      currentPage: 1,
      delShow: false,
      useShow: false,
      followTitle: '',
      btnText: '',
      followShow: false,
      pageSize: 10,
      count: 0,
      rowInfo: '',
      styleOne: styleOne,
      styleTwo: styleTwo,
      styleThree: styleThree,
      copyData: null,
      hasListInfo: '暂无数据',
      singleList: [],
      activeName: 'code',
      createNameList: [],
      sourceList: [],
      channelSearch: {
        keyword: ''
      }
    }
  },
  computed: {
    getSourceChannel() {
      return function(obj) {
        if (obj) {
          return `${obj.first_name}-${obj.second_name}${
            obj.three_name ? '-' + obj.three_name : ''
          }`
        }
        return '/'
      }
    },
    getPersonnel() {
      return function(user) {
        let person = ''
        for (const item of user) {
          person += item.name + ' '
        }
        return person
      }
    }
  },
  created() {
    this.getCodeList()
  },
  methods: {
    onSubmit() {
      this.currentPage = 1
      this.getCodeList(this.channelSearch)
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getCodeList(this.channelSearch)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getCodeList(this.channelSearch)
    },
    resetForm() {
      this.channelSearch = {
        keyword: ''
      }
      this.getCodeList()
    },
    async delConfirm() {
      const { code, message } = await deleteProgramCode(this.rowInfo.id)
      if (code === 10000) {
        this.$message.success(message)
        this.getCodeList(this.channelSearch)
        this.delShow = false
      }
    },
    async downloadCode(id, name) {
      /* eslint-disable no-new */
      const { code, data } = await downloadCode({ id })
      if (code === 10000) {
        var img = new Image()
        img.setAttribute('crossOrigin', 'Anonymous')
        img.onload = function() {
          var canvas = document.createElement('canvas')
          canvas.width = img.width
          canvas.height = img.height
          var ctx = canvas.getContext('2d')
          // 将img中的内容画到画布上
          ctx.drawImage(img, 0, 0, canvas.width, canvas.height)
          // 将画布内容转换为base64
          var base64 = canvas.toDataURL()
          // 创建a链接
          var a = document.createElement('a')
          a.href = base64
          a.download = name
          // 触发a链接点击事件，浏览器开始下载文件
          a.click()
        }
        img.src = data.qrcode
        // 必须设置，否则canvas中的内容无法转换为blob
      }
    },
    inputFormSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.rowInfo.num === 1) this.createFollow()
          if (this.rowInfo.num === 2) this.updateFollow()
        } else {
          return false
        }
      })
    },
    async getCodeList(search) {
      let params = {
        pageSize: this.pageSize,
        page: this.currentPage
      }
      if (search) {
        if (search.keyword) params.scene = search.keyword
        this.hasListInfo = '暂无数据，请新增小程序码'
      } else {
        this.hasListInfo = '暂无数据'
      }
      const { data, code } = await getCodeList(params)
      if (code === 10000) {
        this.count = data.total
        this.list = data.list.map((item, index) => {
          return {
            num: (this.currentPage - 1) * this.pageSize + index + 1,
            id: item._id,
            scene: item.scene,
            remark: item.remark || '/',
            page: '/page/fd/index/index',
            created_at: item.created_at ? this.formatTime(item.created_at) : '-'
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.follow-operation {
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  margin-bottom: 20px;
  /deep/form {
    width: 100%;
    .el-date-editor {
      padding: 0 10px;
      width: 220px;
      height: 32px;
      .el-range-input {
        width: 85px;
        height: 30px;
      }
    }
  }
  .search-item {
    color: #17233d;
    &:not(:nth-child(4)) {
      margin-right: 50px;
    }
    /deep/input {
      width: 220px;
      height: 32px;
      line-height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .arrow {
    float: right;
  }
  .list {
    margin: 20px 0 0;
    width: 100%;
    .title {
      line-height: 32px;
      text-align: right;
      /deep/button {
        margin-bottom: 10px;
        padding: 6px 20px;
        height: 32px;
      }
    }
    /deep/.el-dialog__wrapper {
      button {
        padding: 6px 20px;
        height: 32px;
      }
      .el-dialog__headerbtn {
        top: 10px;
        right: 5px;
      }
    }
    .pagination {
      margin-top: 50px;
      text-align: center;
    }
  }
  /deep/.cell {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
    *:not(button) {
      display: inline-block;
      width: 100%;
    }
  }
  .center {
    line-height: 20px;
    text-align: center;
  }
  .button-wrap {
    button {
      height: 32px;
      line-height: 0;
    }
  }
  .danger-tip {
    margin-top: 10px;
    line-height: 16px;
    color: #f00;
  }
  .el-dialog__wrapper {
    /deep/.el-input__inner {
      width: 220px;
    }
  }
  .demo-ruleForm {
    /deep/.el-input {
      width: 220px;
    }
  }
  .style-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;
    padding: 0 40px;
    line-height: 40px;
  }
  .use-show {
    /deep/.el-dialog__footer {
      padding: 0;
    }
  }
  .btn-style {
    border: 1px solid rgba(228, 230, 233, 1);
    border-radius: 4px;
  }
  /deep/.el-button--mini {
    padding: 2px 5px;
    width: 60px;
    height: 22px;
  }
}
</style>
